<template>
  <div>
    <top-nav title="规则说明设置"></top-nav>
    <div class="wyu-main" v-loading="loading">
      <div class="wyu-block">
        <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
          <p class="tit"> 1）这里设置拼团、砍价、抢购的规则说明</p>
        </div>

        <el-form ref="form" :model="form" label-width="120px" size="mini">
          <div class="wyu-title">
            <h2>拼团规则说明</h2>
          </div>
          <el-form-item label="拼团规则说明" >
            <html-editor title="拼团规则说明" :html="form.pintuan_rule"  v-model="form.pintuan_rule"></html-editor>
          </el-form-item>

          <div class="wyu-title">
            <h2>砍价规则说明</h2>
          </div>
          <el-form-item label="砍价规则说明" >
            <html-editor title="砍价规则说明" :html="form.kanjia_rule"  v-model="form.kanjia_rule"></html-editor>
          </el-form-item>

          <div class="wyu-title">
            <h2>抢购规则说明</h2>
          </div>
          <el-form-item label="抢购规则说明" >
            <html-editor title="抢购规则说明" :html="form.qianggou_rule"  v-model="form.qianggou_rule"></html-editor>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="wyu-btn-div">
      <el-button size="small" type="primary"  @click="setRule()">立即保存</el-button>
      <el-button size="small">取消</el-button>
    </div>
  </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import HtmlEditor from "@/components/HtmlEditor";
    import WyuUpload from "@/components/Photos";

    import * as api from "@/api";
    export default {
        name: 'adminRole',
        components: {TopNav,HtmlEditor,WyuUpload},
        data() {
            return {
                loading: false,

                form: {
                    pintuan_rule: '',
                    kanjia_rule: '',
                    qianggou_rule: '',
                }
            }
        },

        created() {
            this.getRule();
        },

        methods: {
            getRule(){
                this.loading = true;
                api.post("/shop/admin.setting/getRule", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.form = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            setRule() {
                this.loading = true;
                api.post("/shop/admin.setting/setRule", {
                    appToken: this.$store.getters.appToken,
                    pintuan_rule: JSON.stringify(this.form.pintuan_rule),
                    kanjia_rule: JSON.stringify(this.form.kanjia_rule),
                    qianggou_rule: JSON.stringify(this.form.qianggou_rule),
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            }
        }
    }
</script>
